#my-dialog-wrap{position:fixed;width:100vw;height:100vh;background-color:rgba(0,0,0,.6);z-index:99999}.my-dialog{position:fixed;top:50%;left:50%;width:200px;font-size:14px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:#fff;border:2px solid #5f9ea0;box-shadow:0 0 16px #ccc}.my-dialog-title{color:#006a6d;height:26px;line-height:26px;padding:2px 8px;border-bottom:2px solid #5f9ea0}.my-dialog-content{height:96px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.my-dialog-operation{display:-webkit-flex;display:flex;border-top:2px solid #5f9ea0}.my-dialog-operation .cancel,.my-dialog-operation .confirm{-webkit-flex:1 1;flex:1 1;height:30px;line-height:30px;text-align:center}.my-dialog-operation .cancel{color:#ccc}.my-dialog-operation .confirm{color:#666;border-left:2px solid #5f9ea0}.my-dialog-operation .cancel:active,.my-dialog-operation .confirm:active{color:#fff;background-color:rgba(0,0,0,.3)}.my-dialog-operation .cancel:hover,.my-dialog-operation .confirm:hover{background-color:rgba(0,0,0,.1)}