.todos-wrap{position:relative;width:100%;height:calc(100% - 30px);padding-bottom:31px;box-sizing:content-box}.todos-body{position:relative;height:calc(100vh - 86px);overflow-y:auto}.todos-body.not-login{overflow:hidden}.todos-body.not-login::before{-webkit-filter:blur(5px);filter:blur(5px);position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);content:"";width:100%;height:100%;background-color:rgba(127,255,212,.8)}.todos-body.not-login::after{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);content:"去登录";width:100px;height:50px;line-height:50px;background-color:rgba(0,0,0,.2);box-shadow:0 0 5px #fff;cursor:pointer}.todos-body .loading-wrap{position:absolute;top:50%;left:50%;z-index:9999;width:100%;height:100%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:rgba(0,0,0,.2)}.todos-body .loading-wrap .loading{position:absolute;display:inline-block;width:30px;max-width:100%;height:30px;max-height:100%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);vertical-align:middle;-webkit-animation:loading-rotate .8s linear infinite;animation:loading-rotate .8s linear infinite}.todos-body .loading-wrap .loading .loading-svg{color:#fff;font-size:0;display:block;width:100%;height:100%}.todos-body .loading-wrap .loading .loading-svg circle{-webkit-animation:loading-circular 1.5s ease-in-out infinite;animation:loading-circular 1.5s ease-in-out infinite;stroke:currentColor;stroke-width:3;stroke-linecap:round}.todos-operation{display:-webkit-flex;display:flex;position:absolute;left:0;bottom:0;width:100%;height:30px;background:linear-gradient(limegreen, transparent),linear-gradient(90deg, skyblue, transparent),linear-gradient(-90deg, coral, transparent);background-blend-mode:screen}.todos-operation .operation-input{-webkit-flex:1 1;flex:1 1;color:#000;padding:0 10px;border:0 none;outline:0 none;background-color:transparent}.todos-operation .apply{font-size:14px;width:10%;color:#fff;line-height:30px;padding:0 10px;background-color:rgba(0,0,0,.1)}.todos-operation .apply:active{color:#006a6d;background-color:#e9f9fa}.todos-operation .apply:hover{background-color:rgba(0,0,0,.2)}::-webkit-scrollbar{width:5px;height:5px}::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(202,202,202,0);background-color:#fff;border-radius:3px}::-webkit-scrollbar-thumb{border-radius:7px;-webkit-box-shadow:inset 0 0 6px #c2bbbb;background-color:#e8e8e8}::-webkit-scrollbar-corner{background:khaki}@-webkit-keyframes loading-rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loading-rotate{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@-webkit-keyframes loading-circular{0%{stroke-dasharray:1,200;stroke-dashoffset:0}50%{stroke-dasharray:90,150;stroke-dashoffset:-40}100%{stroke-dasharray:90,150;stroke-dashoffset:-120}}@keyframes loading-circular{0%{stroke-dasharray:1,200;stroke-dashoffset:0}50%{stroke-dasharray:90,150;stroke-dashoffset:-40}100%{stroke-dasharray:90,150;stroke-dashoffset:-120}}