.todos-item{height:30px;padding-left:12px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;text-align:left;color:#008b8b;box-sizing:border-box}.todos-item:hover{color:#18b1b1;background-color:#f6f7f7}.item-content{width:88%}.solve-icon,.close-icon{color:#5f9ea0;width:4%;height:20px;line-height:20px;font-size:16px;border:0 none;outline:0 none}.solve-icon:active,.close-icon:active{background-color:#ccc}.solve-icon{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.close-icon{margin:0 2%}.todos-item.is-finish{color:#ccc}.todos-item.is-finish .item-content{text-decoration:line-through}.todos-item.is-finish .solve-icon,.todos-item.is-finish .close-icon{color:#999}.todos-item.is-finish:hover{color:#999}.todos-item.todos-item-edit{outline:1px dashed #18b1b1}.todos-item.todos-item-edit .edit-content{height:100%;-webkit-flex:1 1;flex:1 1;border:0 none;outline:0 none}