.viavacos{display:inline-block;margin:auto;margin-left:24px;margin-right:7px;width:7px;height:7px;border-top:7px solid #f39b9b;border-left:7px dashed #0898af;border-right:7px dotted #fff700;border-bottom:7px solid #989898;border-radius:50%;vertical-align:middle}.todos-header{display:-webkit-flex;display:flex;position:relative;text-align:left;height:55px;line-height:55px;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-app-region:drag;background:linear-gradient(limegreen, transparent),linear-gradient(90deg, skyblue, transparent),linear-gradient(-90deg, coral, transparent);background-blend-mode:screen;overflow:hidden}.todos-header .todos-title{display:inline-block;vertical-align:middle;color:#5f9ea0}.todos-header .search{margin:auto auto auto 10%;display:inline-block;-webkit-app-region:no-drag}.todos-header .close-btn{position:absolute;top:0;right:10px;text-align:center;width:36px;height:100%;font-size:20px;color:#424041;-webkit-app-region:no-drag}.todos-header .close-btn:hover{color:#fff}.todos-header .mini-btn{position:absolute;top:0;right:46px;width:36px;text-align:center;height:100%;font-size:20px;color:#424041;-webkit-app-region:no-drag}.todos-header .mini-btn:hover{color:#fff}.todos-header .user-info{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;position:absolute;right:180px}.todos-header .user-info .head-icon{width:30px;height:30px;background-repeat:no-repeat;background-size:cover;border-radius:50%;margin-right:5px;cursor:pointer}.todos-header .user-info .user-name{color:#efe3e3;font-size:13px}.todos-header .user-info .user-name span{cursor:pointer}.version{margin-left:4px;font-size:12px;padding:0 4px;color:#5f9ea0;vertical-align:top;background-color:rgba(255,255,255,.3)}