.check-box{position:relative;display:-webkit-flex;display:flex;width:12px;height:12px;line-height:12px;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:1px solid #666}.check-box.--solved::before{content:"";width:12px;height:12px;background-color:#32cd32}.check-box.--solved::after{top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);position:absolute;content:"";width:1.8px;height:1.8px;border-top:4.8px solid #1d521d;border-left:4.8px solid #307730;border-right:4.8px solid #098509;border-bottom:4.8px solid #7ddd7d;border-radius:20%;background-color:#135e13}